import { Card, Col, Row } from 'react-bootstrap';
import { useAppSelector } from '../../hooks/useApp';
import { selectUser } from '../../store/slices/userSlice';
import { Navigate, useLocation } from 'react-router';
import styles from './login.module.scss';
import CardHeader from 'react-bootstrap/esm/CardHeader';
import classnames from 'classnames';
import logo from '../../assets/images/logo.svg';
import LoginSection from './LoginSection';

const useRedirect = () => {
  const user = useAppSelector(selectUser);
  const location = useLocation();
  const from: string = location.state?.from?.pathname || '/';
  return { shouldNavigate: user.apiKey?.length > 0, from };
};

export const Login = () => {
  const { shouldNavigate, from } = useRedirect();

  if (shouldNavigate) {
    return <Navigate to={from} replace />;
  }

  return (
    <div className={styles.loginImg}>
      <Row className="justify-content-md-center w-100">
        <Col xl={4} xs={9} className="mx-auto">
          <Card className={styles.transparentBackground}>
            <CardHeader className={classnames(styles.transparentBackground, 'mx-auto', 'd-flex')}>
              <img className={styles.logoImg} src={logo} alt="logo" />
            </CardHeader>
            <Card.Body className={styles.loginForm}>
              <label className={styles.loginFormTitle}>{'T-Connect'}</label>
              <LoginSection />
            </Card.Body>
          </Card>
        </Col>
      </Row>
    </div>
  );
};
