import { ReactComponent as Wifi4Icon } from '../../assets/images/wifi4.svg';
import { ReactComponent as Wifi3Icon } from '../../assets/images/wifi3.svg';
import { ReactComponent as Wifi2Icon } from '../../assets/images/wifi2.svg';
import { ReactComponent as Wifi1Icon } from '../../assets/images/wifi1.svg';
import { wifiStrength } from 'utils/wifi.utils';

export const WifiIconsArr: any[] = [Wifi1Icon, Wifi2Icon, Wifi3Icon, Wifi4Icon];
export const generateWifiIcon = (signalStrength: number, className?: string) => {
  const WifiIcon = WifiIconsArr[wifiStrength(signalStrength)];
  return <WifiIcon className={className ? className : ''} />;
};
