import { useAsync } from 'hooks/useAsync';
import './ClinicViewIframe.css';
import { gwService } from 'services/gw.service';

export const ClinicViewIframe = () => {
  const { value, status } = useAsync(gwService.getDeviceInformation);
  if (status === 'pending') {
    return <></>;
  }
  return (
    <div className="container-iframe">
      <iframe
        className="responsive-iframe"
        src={`https://tuttnauer-hq.github.io/gateway/#/clinic-view?deviceApiKey=${value?.apiKey}&deviceId=${value?.deviceId}`}
      ></iframe>
    </div>
  );
};
