import { ErrorMessage, Field, Formik } from 'formik';
import * as Yup from 'yup';
import classnames from 'classnames';
import styles from './login.module.scss';
import { Button, Container, Form, InputGroup } from 'react-bootstrap';
import { loginTheUser } from '../../store';
import { useAppDispatch } from '../../hooks/useApp';
import { InputPasswordWithEye } from '../../components/InputPasswordWithEye/InputPasswordWithEye.component';

interface Values {
  username: string;
  password: string;
}

const LoginSection = () => {
  const dispatch = useAppDispatch();

  const doLogin = async (values: Values) => {
    dispatch(loginTheUser({ userName: values.username, password: values.password }));
  };

  const generateIsInvalidClassName = (formikProps: any, fieldName: string): string => {
    return formikProps.errors[fieldName] && formikProps.touched[fieldName] ? ' is-invalid' : '';
  };
  const generateClassName = (formikProps: any, fieldName: string): string => {
    return 'form-control' + generateIsInvalidClassName(formikProps, fieldName);
  };

  return (
    <Container>
      <Formik
        initialValues={{
          username: localStorage.getItem('username') || '',
          password: localStorage.getItem('password') || '',
        }}
        validationSchema={Yup.object().shape({
          username: Yup.string().email('Email is not valid').required('Field is required'),
          password: Yup.string().required('Field is required'),
        })}
        onSubmit={async (values, actions) => {
          await doLogin(values as Values);
        }}
        enableReinitialize={false}
      >
        {(formikProps) => (
          <Form onSubmit={formikProps.handleSubmit}>
            <InputGroup className="mb-3">
              <Field
                value={formikProps.values.username}
                onChange={formikProps.handleChange}
                type="text"
                name="username"
                id="Email"
                placeholder={'Email'}
                className={generateClassName(formikProps, 'username')}
                style={{ backgroundColor: 'white' }}
              />
              <ErrorMessage
                name="username"
                component="div"
                className={classnames('invalid-feedback', styles['invalid-feedback'])}
              />
            </InputGroup>
            <InputGroup className="mb-3 is-invalid">
              <InputPasswordWithEye
                placeholder="Password"
                value={formikProps.values.password}
                onChange={formikProps.handleChange}
                style={{ width: '90%', height: '100%' }}
                id="password"
                className={classnames(generateIsInvalidClassName(formikProps, 'password'), 'd-flex')}
                inputClassName={classnames(generateClassName(formikProps, 'password'), styles.psswdField)}
                svgClassName={styles.inputEyeSvg}
              />
              <ErrorMessage
                name="password"
                component="div"
                className={classnames('invalid-feedback', styles['invalid-feedback'])}
              />
            </InputGroup>

            <InputGroup className="mb-3">
              <Button type="submit" className={classnames('d-block', 'mx-auto', 'w-100', styles.loginButton)}>
                {'Login'}
              </Button>
            </InputGroup>
          </Form>
        )}
      </Formik>
    </Container>
  );
};
export default LoginSection;
